.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}
@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../fonts/lato/Lato-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswald/oswaldmedium.woff2") format("woff2"), url("../fonts/oswald/oswaldmedium.woff") format("woff");
  font-display: swap;
}
img {
  max-width: 100%;
  height: auto;
  display: block;
}
.page {
  width: 100%;
  max-width: 100%;
  height: 100%;
}
.page__body {
  font-family: "Lato", "Arial", sans-serif;
  font-size: 16px;
  line-height: 24px;
  color: #000000;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  background-color: #ffffff;
  min-height: 100%;
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.main-header {
  font-size: 20px;
  line-height: 20px;
  font-family: "Oswald", "Tahoma", sans-serif;
  font-weight: 400;
  margin: 0;
  padding: 0;
  position: relative;
  font-weight: 500;
  padding: 11px 20px;
  padding-bottom: 0;
  width: 100vw;
  min-width: 320px;
  box-sizing: border-box;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .main-header {
    font-size: 18px;
    line-height: 24px;
    font-family: "Oswald", "Tahoma", sans-serif;
    margin: 0;
    padding: 0;
    font-weight: 400;
    min-width: 768px;
    padding: 0 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
@media (min-width: 1440px) {
  .main-header {
    font-size: 20px;
    line-height: 30px;
    font-family: "Oswald", "Tahoma", sans-serif;
    margin: 0;
    padding: 0;
    font-weight: 400;
    width: 1220px;
    margin: 53px auto 0;
  }
}
.main-header__wrapper {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
  padding-bottom: 18px;
}
@media (max-width: 767px) {
  .main-header__wrapper::after {
    position: absolute;
    content: "";
    width: 100vw;
    height: 1px;
    background-color: #d9d9d9;
    top: 66px;
    left: 0;
  }
}
@media (min-width: 768px) {
  .main-header__wrapper {
    padding-bottom: 0;
  }
}
.main-header__toggle {
  width: 24px;
  height: 18px;
  margin-top: 4px;
  background-color: #ffffff;
  border: none;
  background-repeat: no-repeat;
  cursor: pointer;
}
@media (min-width: 768px) {
  .main-header__toggle {
    display: none;
  }
}
.main-header__toggle--closed {
  background-image: url("../img/main-header__toggle--closed.svg");
}
.main-header__toggle--opened {
  background-image: url("../img/main-header__toggle--opened.svg");
  background-size: contain;
  height: 22px;
}
.main-header__toggle--nojs {
  background-image: none;
  cursor: auto;
}
.main-header__link {
  cursor: pointer;
}
.main-header__link:hover {
  opacity: 0.8;
}
.main-header__link:active {
  opacity: 0.6;
}
@media (min-width: 768px) and (max-width: 1439px) {
  .main-header__link {
    margin-top: -6px;
  }
}
@media (max-width: 767px) {
  .main-header__link--text {
    margin-top: 5px;
    margin-right: 5px;
  }
}
@media (min-width: 768px) {
  .main-header__link--text {
    display: none;
  }
}
.main-container {
  flex-grow: 1;
  width: 100vw;
  min-width: 320px;
}
@media (min-width: 768px) {
  .main-container {
    min-width: 768px;
  }
}
@media (min-width: 1440px) {
  .main-container {
    min-width: 1440px;
  }
}
.main-footer {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.main-footer__wrapper {
  width: 100vw;
  min-width: 320px;
  padding: 40px 20px;
  display: flex;
  gap: 40px;
  flex-direction: column;
  align-items: center;
  background-color: #f2f2f2;
  box-sizing: border-box;
}
@media (max-width: 767px) {
  .main-footer__wrapper {
    position: relative;
  }
  .main-footer__wrapper::before {
    position: absolute;
    content: "";
    width: calc(100% - 40px);
    left: 20px;
    top: 84px;
    height: 1px;
    background-color: #d9d9d9;
  }
  .main-footer__wrapper::after {
    position: absolute;
    content: "";
    width: calc(100% - 40px);
    left: 20px;
    bottom: 94px;
    height: 1px;
    background-color: #d9d9d9;
  }
}
@media (min-width: 768px) {
  .main-footer__wrapper {
    min-width: 768px;
    padding: 55px 30px;
    flex-direction: row;
    justify-content: space-between;
    box-sizing: border-box;
  }
}
@media (min-width: 1440px) {
  .main-footer__wrapper {
    padding: 66px;
    padding-left: calc((100vw - 1220px) / 2);
    padding-right: calc((100vw - 1220px) / 2);
  }
}
.main-footer__logo-text {
  height: 24px;
}
@media (min-width: 768px) {
  .main-footer__logo-text {
